(function () {
    'use strict';
    var angular = require('angular');
    require('./final-thankyou.component.scss');
    require('./final-thankyou.component.service');

    var complete = angular.module('app').component('thankYouPage', {
        template: require('./final-thankyou.component.html'),
        controller: ['$rootScope', '$http', '$state', '$anchorScroll', 'analyticService', 'appState', 'CONFIG', 'ThankYouService',
            function ($rootScope, $http, $state, $anchorScroll, analyticService, appState, CONFIG, ThankYouService) {
                var vm = this;
                vm.$onInit = function () {
                    //var status = $state.params.saveData.statusCode;
                    vm.autoUrl = "https://ir99txrb18.execute-api.ap-south-1.amazonaws.com/prod/kyc/v1/web/ekyc/callback?tokenId=";
                    vm.frnNumber = $state.params.saveData.frnNumber;
                   // vm.dipArnNumber = $state.params.saveData.dipRefNumber;
                    vm.ekycToken = $state.params.saveData.ekycToken;
                    console.log('auto data from back====' +vm.frnNumber);
                    // console.log('autoURL====' + vm.autoUrl+vm.ekycToken);

                }
                vm.routeautoUrl = function () {
                    console.log('autoURL====' + vm.autoUrl +vm.ekycToken);
                    window.open("https://ir99txrb18.execute-api.ap-south-1.amazonaws.com/prod/kyc/jOefo5t6", "_blank");
                    window.open(vm.autoUrl +vm.ekycToken, "_blank");
                    var securityParams = {
                        robocheck: vm.robocheck ? vm.robocheck : ''
                    };
                    console.log('data to karza====' + vm.frnNumber + "and==",securityParams);
                    ThankYouService.karzaPostDip(securityParams,vm.frnNumber.replaceAll('/','-'));
                                      

                };
            }]
    });

    module.exports = complete.name;
})();



