(function () {
    'use strict';

    var ThankYouService = angular.module('app').service('ThankYouService', ['$rootScope', '$http', '$q', '$timeout', '$interval', 'appState', 'CONFIG', function ($rootScope, $http, $q, $timeout, $interval, appState, CONFIG) {
   
        var self = this;
        self.arn = null;

        self.karzaPostDip = function (securityParams,frnNumber) {
            var apiURL, httpMethod;
            console.log('arn==='+frnNumber);

            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                apiURL = CONFIG.apiUrl + '/bankBazaar/karza/postDipCheck/' + frnNumber + '?hash_id=' + Math.random();

            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/step3_personal-details/postdipCheck.json';
            }
            return $http({
                method: httpMethod,
                url: apiURL,
                headers: {
                    'Content-Type': 'application/json'
                },
                // data: JSON.stringify(reqData),
                params: securityParams,
                //hideLoader: true
            }).then(function (response) {
                console.log("saveEkycData==",response);
                if (response.status === 200 ) {
                   console.log(response.status);
                }
            }, function (error) {
                console.log("Sorry! there ssems to be some technical error.");
            });
        };

   
    }]);
    module.exports = ThankYouService.name;
})();        


